import React, { Component } from 'react';
import {Link } from 'react-router-dom'; 
import tweet from './tweet.css';

class Tweet extends Component {
    constructor(props){
        super(props);
        this.tweetId = React.createRef();
        this.linkStyle={
            color:'#2c3b40',
            textDecoration: 'none',
            fontSize: '1em',
        }

    }
    
    addLikes=()=>{
        const url = `api/v1/tweets/like/${this.props.id}`;
        fetch(url)
        .then(response=>response.json())
        .then(data=>console.log(data))
      }
    render(
    ) { 
        return ( 
            <div className="tweet">
                <img src="http://via.placeholder.com/50" />
                <p>{this.props.username}</p>
                <p>
                {this.props.tweet}
                </p>
                <p>{this.props.likes.length}</p>
                <span 
                    className={this.props.likes<1?"likes fa fa-heart":"likes fa fa-heart red-heart"} 
                    aria-hidden="true" onClick={this.addLikes}>{this.props.likes} likes</span>
                <span className="retweets">{this.props.retweets} retweets</span>
               <span className="comments">
                    <Link style={this.linkStyle} to={`/comments/${this.props.id}`}>
                        {this.props.comments} {this.props.comments==1?"comment":"comments"}
                    </Link>
                </span>
               
            </div>
         );
    }
}
 
export default Tweet;